// ======= Start Media Query mixin ===== //

@mixin breakpoint($point) {
  @if $point ==small {
    @media (max-width: 575px) {
      @content;
    }
  }

  @else if $point ==phone {
    @media (min-width: 576px) {
      @content;
    }
  }

  @else if $point ==mobile {
    @media (max-width: 767px) {
      @content;
    }
  }

  @else if $point ==tablet {
    @media (min-width: 768px) {
      @content;
    }
  }

  @else if $point ==laptop {
    @media (min-width: 992px) {
      @content;
    }
  }

  @else if $point ==desktop {
    @media (min-width: 1200px) {
      @content;
    }
  }

  @else if $point ==tv {
    @media (min-width: 1400px) {
      @content;
    }
  }
}

// ======== End Media Query mixin ====== //
/* ========= Start Global Rules =========*/

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

html {
  font-size: 100%;
  scroll-behavior: smooth;
}

body::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}

body::-webkit-scrollbar-track {
  background-color: lightblue
}

body::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: lightblue;
}

body::-webkit-scrollbar-corner {
  border-radius: 10px;
  background-color:lightblue;
}

.container {
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;

  // === tablet screens === //
  @include breakpoint(tablet) {
    width: 750px;
  }

  // === laptop screens === //
  @include breakpoint(laptop) {
    width: 970px;
  }

  // === desktop screens === //
  @include breakpoint(desktop) {
    width: 1170px;
  }

  // === tv screens === //
  @include breakpoint(tv) {
    width: 1370px;
  }
}

video {
  height: auto;
  max-width: 100%;
}

img {
  height: auto;
  max-height: 100%;
  max-width: 100%;
}

ul {
  list-style: none;
}

::selection {
  color: #fff;
  background: var(--primary-clr);
}

/* ========= End Global Rules ===========*/

/* ====== Start Typography Rules ========*/

body {
  line-height: 1.5;
  overflow-x: hidden;
  font-family: var(--body-font);
  background-color: var(--white-clr);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
  font-family: var(--title-font);
}

h1 {
  font-size: 1.5rem;
  margin-bottom: .1875rem;

  @include breakpoint(desktop) {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}

// h3 {
//   font-size: rem(24);
//   margin-bottom: rem(24);
// }

a,
a:visited,
a:active {
  text-decoration: none;
}

a:visited {
  color: lightblue;
}

/* ======= End Typography Rules =========*/
/* ========== Start Variables ===========*/

:root {

  /* === Family Fonts === */
  --body-font: "open Sans", sans-serif;
  --title-font: "Merriweather", sans-serif;

  /* === Light colors === */
  --white-clr: #fff;
  --cultured-clr: #F0F4F5;

  /* === Colorful colors === */
  --primary-clr: #0059B2;
  --red-color: #731904;

  /* === Dark Colors === */
  --black-clr: #303030;

  /* === Box Shadows === */
  --box-shadow: 0px 0px 20px rgba(152, 152, 152, 0.33);

  /* === Line Height === */
  --line-height: 1.6;
  --line-height-lg: 2;

  /* === Transition === */
  --transition: 0.5s;

  /* === Border Radius === */
  --border-radius: 5px;
  --border-radius-lg: 10px;

  /* === Spaces === */
  --space-sm: 0.625rem;
  --space-md: 1.25rem;
  --space-lg: 3.125rem;
  --space-xlg: 6.25rem;

}

/* =========== End Variables ============*/
/* ========== Start light Theme =========*/
body.light {
  --primary-clr: #0059B2;
  --white-clr: #fff;
  --black-clr: #030303;
}
/* =========== End light Theme ==========*/
/* ========== Start Dark Theme ==========*/
@media (prefers-color-scheme: dark) {
  :root {
    --primary-clr: #ff8f24;
    --white-clr: #303030;
    --black-clr: #fff;
  }
}

body.dark {
  --primary-clr: #ff8f24;
  --white-clr: #303030;
  --black-clr: #fff;
}

/* =========== End Dark Theme ===========*/
