/* ========= End common Styles ========= */
/* =========== Start Header ===========  */

header {
  .content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    height: 40px;
    border-bottom: 1px solid var(--cultured-clr);
    .logo {
      position: absolute;
      top: 45px;
      height: 30px;
      width: 30px;
      line-height: 26px;
      text-align: center;
      border-radius: 50%;
      user-select: none;
      text-transform: capitalize;
      border: 1px solid var(--primary-clr);
      border-left-style: dotted;
    }
    > ul {
      display: flex;
      align-items: center;
      justify-content: space-between;
      li {
        height: 20px;
        width: 20px;
      }
      li:not(:last-child) {
        margin-right: 10px;
      }
    }
    .options {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    button {
      width: 20px;
      height: 20px;
      cursor: pointer;
      margin-right: 10px;
      img {
        width: 100%;
      }
    }
    .website__lang {
      position: relative;
      padding-left: 10px;
      cursor: pointer;
      z-index: 3;
      border-left: 1px solid var(--black-clr);
      img:first-child {
        padding-right: 3px;
      }
      &.open {
        .lang__list {
          display: block !important;
        }
        .arrow {
          transform: rotate(180deg);
        }
      }
    }
    .arrow {
      position: relative;
      top: 3px;
      width: 15px;
      transition: var(--transition);
    }
    .lang__list {
      display: none;
      position: absolute;
      top: 0;
      left: 0;
      transition: var(--transition);
      background-color: rgb(2, 73, 86);
      a {
        display: flex;
        align-items: center;
        justify-content: space-around;
        padding: 3px 10px;
        &:hover {
          background-color: #731904;
        }
      }
      span {
        font-size: 12px;
        color: var(--black-clr);
      }
    }
  }
  nav {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    position: relative;
    max-height: 40px;
    padding-block: 10px;
    .toggle__manu {
      height: 19px;
      width: 25px;
      cursor: pointer;
      transition: var(--transition);
      transition-delay: 0.4s;
      span {
        display: block;
        height: 3px;
        width: 100%;
        transition: var(--transition);
        background-color: var(--primary-clr);
      }
      span:not(:last-child) {
        margin-bottom: 5px;
      }
    }
    .toggle__manu.open {
      transform: rotate(-45deg);
      span:first-child {
        transform: translateY(10px);
        transition-delay: 0.2s;
        background-color: red;
      }
      span:nth-child(2) {
        transform: translateX(-15px);
        opacity: 0;
      }
      span:last-child {
        transform: rotate(-90deg) translateX(6px);
        transition-delay: 0.2s;
        background-color: red;
      }
    }
    .toggle__manu.open ~ ul {
      opacity: 1 !important;
      z-index: 2 !important;
      bottom: -113px !important;
    }
    ul {
      display: grid;
      grid-template-columns: auto auto auto;
      position: absolute;
      bottom: -130px;
      width: 100%;
      opacity: 0;
      z-index: -2;
      transition: var(--transition);
      transition-delay: 0.2s;
      background-color: var(--white-clr);
      li {
        padding-block: 10px;
        &:hover {
            background-color: #8e35217f;
        }
      }
      a {
        text-align: center;
        img {
          display: block; 
          width: 15px;
          margin: 0 auto;
        }
        span {
          display: block;
          font-size: 14px;
          color: var(--black-clr);
        }
      }
    }
  }
}

